// Получить признак равенства координат
export const coordsCompare = (one, two) => {
    return one[0] === two[0]
        && one[1] === two[1];
};

// Получить признак того, что заданные координаты находятся в пределах нашей сетки
export const isCoordPossibleToMove = (coord, rMax = 4, cMax = 4) => {
    return coord[0] >= 0 && coord[0] < rMax
        && coord[1] >= 0 && coord[1] < cMax;
}

// Получить список координат с возможными перемещениями элемента в пределах нашей сетки
export const getPossibleCoordsToMove = (fromCoord) => {
    // Исходные координаты элемента
    let row = fromCoord[0];
    let col = fromCoord[1];

    // Возможные перемещения: вверх, вниз, влево, вправо; фильтруем по границам сетки...
    return [
        [row - 1, col],
        [row + 1, col],
        [row, col - 1],
        [row, col + 1]
    ].filter((coords) => isCoordPossibleToMove(coords)); // фильтруем по границам сетки... 
};

// Проверить возможность перемещения элемента на место пустого элемента
export const isEmptyCoordPossibleToMove = (fromCoord, emptyCoord) => {
    // Проверяем: есть ли в списке возможных перемещений координаты "пустого" элемента
    let res = getPossibleCoordsToMove(fromCoord).filter(coord => coordsCompare(coord, emptyCoord));

    // Если такой элемент есть, то true, иначе false
    return res && res[0] ? true : false;
};

// Получить признак нахождения элемента в правильной позиции
export const isRightCoordPosition = (coord, value, rMax = 4, cMax = 4) => {
    // Правильная позиция для элемента с номером "0" - это [3, 3]
    if (!value) {
        return coord[0] === rMax - 1 && coord[1] === cMax - 1;
    }

    // Правильная позиция для остальных соответствут индексу элемента в массиве + 1
    return value === (coord[0] * cMax + coord[1] + 1);
}

// Получить номер элемента сетки (объекта в массиве) по заданным координатам
export const findIndexByCoord = (grid, coord) => {
    return grid.findIndex(item => coordsCompare(item.coord, coord));
};

// Получить массив элементов вокруг заданного
export const getElementsAround = (grid, select_coord) => {
    return getPossibleCoordsToMove(select_coord).map(coord => {
        return grid[findIndexByCoord(grid, coord)];
    });
}
