
import { ACTION_MOVE, ACTION_NEW_GAME } from './actions';
import { isEmptyCoordPossibleToMove, isRightCoordPosition } from './../helpers/gridFinder';
import { getMovedGrid } from './../helpers/gridMove';
import generateGridState from './../helpers/gridGenerator';

// Исходное состояние
const initialState = {
    started: false,         // Признак начала игры
    moves: 0,               // Количество ходов
    ...generateGridState()  // Состояние и параметры сетки
};

// Редуктор (функция обработки действий...)
const rootReducer = (state = initialState, action) => {
    // По типу действия
    switch (action.type) {
        case ACTION_MOVE:   // Если действие определяет перемещение
            
            // Координаты перемещаемого элемента
            let fromCoord = action.payload;

            // Координаты "пустого" элемента (элемент с номером "0") 
            let toCoord = state.grid.find(item => !item.value).coord;

            // Если возможно перемещение выбранного элемента на место пустого элемента, то выполнить такое перемещение
            let grid = isEmptyCoordPossibleToMove(fromCoord, toCoord) && getMovedGrid(state.grid, fromCoord, toCoord);

            // Если перемещение оказалось невозможным, то возвращаем неизменное состояние
            if (!grid) {
                return state;
            }

            // Обновляем признак правильной позиции для каждого элемента сетки
            grid = grid.map(item => {
                item.done = isRightCoordPosition(item.coord, item.value);
                return item;
            });

            // Выбираем элементы, которые находятся в правильной позиции
            let gridDone = grid.filter(item => item.done);

            return {
                ...state,
                started: true,                          // Признак начала игры
                win: gridDone.length === grid.length,   // Если количество элементов в сетки равно количеству элементов в правильных позициях (т.е. все на своих местах), то победа!
                moves: state.moves + 1,                 // Инкремент счетчика ходов
                grid: grid                              // Новая сетка, с учетом выполненного перемещения
            };

        case ACTION_NEW_GAME: // Если действие определяет переход в состояние "Новая игра"
            return {...initialState,
                    ...generateGridState()};

        default:    // Если другой тип действия, то состояние приложения не изменяется
            return state;
    }
};

export default rootReducer;