// Определяем действия

// Тип действия "Перемещение"
export const ACTION_MOVE = 'ACTION_MOVE';
// Тип действия "Новая игра"
export const ACTION_NEW_GAME = 'ACTION_NEW_GAME';

// Действие "Перемещение"
export const doMove = (from) => {
    return {
        type: ACTION_MOVE,
        payload: from       // Координаты перемещаемого элемента
    };
};

// Действие "Новая игра"
export const doNewGame = () => {
    return {
        type: ACTION_NEW_GAME,
        payload: null
    };
};
