import { isRightCoordPosition } from './gridFinder';

// Получить целое число в заданом диапазоне
function getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
}

// Генерация сетки
const generateGrid = (rMax, cMax) => {
    // Общее количество элементов
    let size = rMax * cMax;

    // Массив индексов по порядку
    let plain = (new Array(size)).fill(0).map((_, i) => i);

    // Сетка с параметрами и значением элемента, который в этой ячейки сетки находится
    let grid = [];

    // Заполняем сетку параметрами и значениями
    for (let r = 0; r < rMax; r++) {
        for (let c = 0; c < cMax; c++) {
            // Объект, опеределяющий элемент сетки
            let item = {
                coord: [r, c], // Координаты элемента сетки
                value: plain.splice(getRandomInt(0, plain.length), 1)[0], // Для получения значения вырезаем случайный элемент из массива индексов
            };

            // Признак нахождения элемента в правильной позиции
            item.done = isRightCoordPosition(item.coord, item.value);

            // Добавляем элемент в сетку
            grid.push(item);
        }
    }

    return grid;
};

// Генерация полей объекта состояния приложения, определяющих данные сетки
const generateGridState = (rMax = 4, cMax = 4) => {
    let state = {};

    // grid - Сетка с параметрами и значением элемента, который в этой ячейки сетки находится
    state.grid = generateGrid(rMax, cMax);

    // Признак "Победа!" - когда все элементы находятся в правильной позиции
    state.win = state.grid.filter(item => item.done).length === state.grid.length;

    return state;
};

export default generateGridState;
