import React from 'react';
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { doMove, doNewGame } from './../../store/actions';
import Puzzle from './../Puzzle/Puzzle';
import Winner from './../Winner/Winner';
import { getElementsAround } from './../../helpers/gridFinder';
import { createSmartappDebugger, createAssistant } from "@sberdevices/assistant-client";
import './Puzzles.css';

// Инициализация асистента
const initializeAssistant = (getState) => {
    // console.log('NODE_ENV =', process.env.NODE_ENV);

    if (process.env.NODE_ENV === "development") {
      return createSmartappDebugger({
        token: process.env.REACT_APP_TOKEN ?? "",
        initPhrase: `Запусти ${process.env.REACT_APP_SMARTAPP}`,
        getState,
      });
    }
    return createAssistant({ getState });
};

// Пазлы: контейнер, в который помещаем 15 пазлов
class Puzzles extends React.Component {

    constructor(props) {
        super(props);
    
        // Инициализируем асистента
        this.assistant = initializeAssistant( () => this.getStateForAssistant() );

        // Регистрируем событие для onData
        this.assistant.on("data", (event) => {
            // console.log(`assistant.on(data)`, event);
            const { action } = event
            this.dispatchAssistantAction(action);
        });

        // Регистрируем событие для onStart
        this.assistant.on("start", (event) => {
            // console.log(`assistant.on(start)`, event);
        });
    
    }
    
    // Получить состояние для передачи ассистенту
    getStateForAssistant () {
        // console.log('getStateForAssistant: puzzles:', this.props.puzzles)

        // Координаты "пустого" элемента (элемент с номером "0") 
        let coord = this.props.puzzles.find(item => !item.value).coord;

        // Получить массив индексов элементов вокруг заданного
        const stateAssist = {
            item_selector: {
                items: getElementsAround(this.props.puzzles, coord)
          }
        };

        // console.log('getStateForAssistant: stateAssist:', stateAssist)
        return stateAssist;
    }
    
    // Обработка действий, полученных от ассистента
    dispatchAssistantAction (action) {
        // console.log('dispatchAssistantAction', action);
        if (action) {
            switch (action.type) {
                case 'on_click_puzzle':
                    action.item && this.props.onPuzzleClick(action.item.coord)
                    return;
                        
                case 'reset_game':
                    this.props.onNewGame();
                    return;
        
                default:
                    throw new Error();
            }
        }
    }

    render() {
        // Создаем массив...
        let puzzleArray = this.props.puzzles.map((item, index) => {
            return (
                <Puzzle
                    key={'item:' + index}
                    {...item}
                    onClick={() => item.value && this.props.onPuzzleClick(item.coord)}
                />
            );
        });
        
        // Добавляем массив в контейнер + элемент "Победа!"...
        return (
            <div className="puzzles-wrapper">
                <div className="puzzles">
                    {puzzleArray}
                </div>
                <Winner/>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    puzzles: state.grid
});

const mapActionsToProps = (dispatch) => ({
    onPuzzleClick: bindActionCreators(doMove, dispatch),
    onNewGame: bindActionCreators(doNewGame, dispatch)
});

export default connect(mapStateToProps, mapActionsToProps)(Puzzles);
