// Ключ, с которым сохраняется состояние приложения в localStorage
const STORAGE_KEY = 'fifteenpuzzle:state';

// Загрузить состояние приложения из localStorage
export const loadState = () => {
    try {
        const serializedState = localStorage.getItem(STORAGE_KEY);
        if (serializedState === null) {
            return;
        }

        return JSON.parse(serializedState);
    } catch (e) {
        return;
    }
};

// Сохранить состояние приложения в localStorage
export const saveState = (state) => {
    try {
        const serializedState = JSON.stringify(state);

        localStorage.setItem(STORAGE_KEY, serializedState);
    } catch (e) {}
};

// Удалить состояние приложения из localStorage
export const clearState = () => {
    try {
        localStorage.removeItem(STORAGE_KEY);
    } catch (e) {}
};
