import React from 'react';
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { doNewGame} from './../../store/actions';
import './Status.css';

class Status extends React.Component {
    render() {
        return (
            <div className="status">
                <button
                    className="status-button"
                    onClick={() => {this.props.onNewGameClick()}}
                ><i>Новая игра</i></button>

                <span className="status-text">
                    <i>Количество ходов: {this.props.moves}</i>
                </span>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    moves: state.moves
});

const mapActionsToProps = (dispatch) => ({
    onNewGameClick: bindActionCreators(doNewGame, dispatch)
});

export default connect(mapStateToProps, mapActionsToProps)(Status);
