import { findIndexByCoord } from './gridFinder';

// Переместить элемент из fromCoord в toCoord
export const getMovedGrid = (grid, fromCoord, toCoord) => {
    // Получаем интекс перемещаемого элемента
    let fromPosition = findIndexByCoord(grid, fromCoord);

    // Получаем индекс: куда будем перемещать
    let toPosition = findIndexByCoord(grid, toCoord);

    // Проверяем: если вдруг вышли на пределы массива, то выходим...
    if (fromPosition === -1 || toPosition === -1) {
        return;
    }

    // Создаем копию массива (копируя свойства каждого объекта)
    let gridNew = grid.slice(0).map(item => Object.assign({}, item));

    // Меняем местами элементы
    let tmp = gridNew[fromPosition].coord.slice(0);
    gridNew[fromPosition].coord = gridNew[toPosition].coord.slice(0);
    gridNew[toPosition].coord = tmp;

    return gridNew;
};
