import React from 'react';
import ReactDOM from 'react-dom';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import rootReducer from './store/reducers';
import App from './components/App/App';
import { loadState, saveState, clearState } from './store/localStorage';
import './index.css';

// Загружаем состояние приложения из localStorage (localStorage - локальное хранилище)
const persistedState = loadState();

// Создаем хранилище состояния приложения
const store = createStore(rootReducer, persistedState);

// Регистрируем обработчик на изменение состояния приложения
store.subscribe(() => {
    const state = store.getState(); // Получаем текущее состояние

    // Если установлен признак победы или еще не было ни одного перемещения
    if (state.win || !state.started) {
        clearState();       // Удаляем состояние приложения из localStorage
    } else {
        saveState(state);   // Сохраняем состояние приложения в localStorage
    }
});

ReactDOM.render(
    <Provider store={store}>
        <App/>
    </Provider>,
    document.getElementById('root')
)
