import React from 'react';
import './Puzzle.css';

// Один пазл
class Puzzle extends React.Component {
    render() {
        // Создаем стиль элемента...       
        let style = {
            top: this.props.coord[0] * 25 + '%',
            left: this.props.coord[1] * 25 + '%'
        };
    
        // Добавляем классы стилей...
        let className = ['puzzle'];
        if (!this.props.value) {
            className.push('puzzle-empty');
        }

        // Добавляем классы стилей...
        if (this.props.done) {
            className.push('puzzle-done');
        }
    
        return (
            <div className={className.join(' ')}
                 style={style}
                 onClick={this.props.onClick}>
                <span className="puzzle-inner">{this.props.value || ''}</span>
            </div>
        );
    }
}

export default Puzzle;
